import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { navigate } from 'gatsby'

const BookButton = ({id, title, variant, featured, full, callback}) => {
  const [disabled, setDisabled] = useState(false);

  const onClick = () => {
    setDisabled(true);
    const interval = setInterval(() => {
      callback();
      setDisabled(false);
      clearInterval(interval);
    }, 250);
  }

  return (
    <>
      <button className={`book-button p-5 ${disabled ? 'disabled flex' : ''} ${full ? 'w-full' : ''}`}
              onClick={() => callback ? onClick() : navigate(`/details`, {
                state: {
                  pageId: id,
                  featured,
                },
              })}
              style={{
                backgroundColor: variant,
                color: variant === 'black' ? 'white' : 'black',
              }}
              disabled={disabled}
      >
        {
          !disabled ? '' :
          <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
        }
        {title}
      </button>
    </>
  )
}

BookButton.propTypes = {
  id: PropTypes.string,
  variant: PropTypes.string,
  title: PropTypes.string,
  featured: PropTypes.bool,
  full: PropTypes.bool,
}

BookButton.defaultProps = {
  id: '',
  variant: 'black',
  title: 'Book experience',
  featured: false,
  full: false,
}

export default BookButton
